.modal-subs__hide {
  display: none;
}

.modal-subs__show {
  display: block;
}

.modal-subs {
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: #0009;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.modal-subs-wrapper {
  max-width: 500px;
  margin: 70px auto;
}

.modal-subs-content {
  width: 100%;
  max-height: 80vh;
  background-color: #0c111b;
  border: 2px solid #fff;
  border-radius: 20px;
  padding: 40px;
  position: relative;
  overflow-y: auto;
}

.modal-subs-content:not(:last-child) {
  margin-bottom: 30px;
}

.modal-subs__close {
  width: 36px;
  height: 36px;
  text-align: center;
  color: #fff;
  opacity: .8;
  cursor: pointer;
  background-color: #0000;
  border: none;
  padding: 5px;
  font-size: 38px;
  font-weight: 700;
  position: absolute;
  top: 8px;
  right: 14px;
}

.modal-subs__title {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 30px;
  font-size: 22px;
}

.modal-subs__mail {
  width: 300px;
  height: 50px;
  text-align: center;
  color: #fff;
  background-color: #0c111b;
  border: 1px solid #fff;
  border-radius: 20px;
  outline: none;
  margin: 20px auto;
  padding: 0 20px;
  font-size: 18px;
  display: block;
}

.modal-subs__mail::placeholder {
  color: #fff;
}

.modal-subs__mail:focus, .modal-subs__btn:focus, .modal-subs__close:focus {
  outline: 2px solid #fff;
}

.modal-subs__btn {
  width: 342px;
  height: 50px;
  text-align: center;
  color: #fff;
  background-color: #0c111b;
  border: 1px solid #fff;
  border-radius: 20px;
  margin: 0 auto;
  padding: 0 20px;
  font-size: 18px;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;
  display: block;
}

.modal-subs__btn:hover {
  color: #0c111b;
  background-color: #fff;
}

.modal-subs__error {
  text-align: center;
  color: #fff;
  margin-top: 25px;
  font-size: 12px;
}

@media (max-width: 767px) {
  .modal-subs-wrapper {
    max-width: 420px;
    margin: 50px auto;
  }
}

@media (max-width: 484px) {
  .modal-subs-wrapper {
    max-width: 300px;
    margin: 50px 26px;
  }

  .modal-subs-content {
    padding: 25px;
  }

  .modal-subs-content:not(:last-child) {
    margin-bottom: 25px;
  }

  .modal-subs__close {
    font-size: 28px;
    top: 5px;
    right: 9px;
  }

  .modal-subs__title {
    margin-bottom: 20px;
    font-size: 16px;
  }

  .modal-subs__mail {
    width: 238px;
    height: 40px;
    margin: 14px auto;
    font-size: 16px;
  }

  .modal-subs__btn {
    width: 240px;
    height: 40px;
    font-size: 16px;
  }
}

@media (max-width: 376px) {
  .modal-subs-wrapper {
    margin: 50px 10px;
  }

  .modal-subs-content {
    padding: 15px;
  }

  .modal-subs__close {
    font-size: 35px;
    top: 0;
  }

  .modal-subs__title {
    margin-top: 25px;
